  readonly actions = [
    { icon: "archive", desc: "Archive", emit: "archive" },
    { icon: "cloud_download", desc: "Export to spreadsheet", emit: "download" },
    { icon: "playlist_add", desc: "Add to run", emit: "list-add" },
  ];
}
